
import XImg from 'vux/src/components/x-img/index.vue'

import {exchangeLog} from '../config'
import VScroll from '../components/VScroll'
export default {
  head: {
    title: {
      inner: '兑换记录'
    }
  },
  data () {
    return {
      height: '',
      loading: false,
      form: {
        userId: 0,
        limit: 5,
        pageIndex: 0
      },
      list: [], // 下拉更新数据存放数组
      more: []  // 上拉更多的数据存放数组
    }
  },
  components: {
    XImg,
    VScroll
  },
  mounted () {
    if (this.$route.params.userId === 'null') {
      this.$router.replace('/login')
    } else {
      this.form.userId = JSON.parse(this.$localStorage.get('userInfo')).userId
      this.getList(() => {}, null)
    }
  },
  methods: {
    getList (done, status) {
      const This = this
      this.$http({
        method: 'jsonp',
        url: exchangeLog,
        jsonp: 'callback',
        jsonpCallback: 'json',
        params: this.form,
        before: () => {
          if (status) {
            this.list = []
          }
        }
      })
      .then(res => {
        console.log(res.body.data)
        res.body.data.productList.forEach(el => {
          This.list.push(el)
        })
        if (res.body.data.productList.length < This.form.limit) {
          this.$el.querySelector('.load-more').innerHTML = '我是有底线的！'
        }
        done()
      })
    },
    onRefresh (done) {
      this.form.pageIndex = 0
      this.getList(done, 1)
    },
    onInfinite (done) {
      this.form.pageIndex ++
      this.getList(done, 0)
      // this.$http({
      //   method: 'jsonp',
      //   url: exchangeLog,
      //   jsonp: 'callback',
      //   jsonpCallback: 'json',
      //   params: this.form,
      //   before: () => {
      //     this.loading = true
      //   }
      // })
      // .then(res => {
      //   console.log(res)
      //   let vm = this
      //   let arr = res.body.data.productList
      //   arr.forEach(function (el) {
      //     This.list.push(el)
      //   })
      //   if (arr.length < vm.form.limit) {
      //     this.$el.querySelector('.load-more').innerHTML = '我是有底线的！'
      //     return
      //   }
      //   done()
      // })
    }
  }
}



